import React, { Component } from 'react';
import { Button, Card, CardBody, CardHeader, Col, Pagination, PaginationItem, PaginationLink, Row, Table } from 'reactstrap';
import dataJSON from '../../../data/data.json';

class Tables extends Component {
  constructor(props) {
    super(props)

    this.state = {
      results: dataJSON
    }
  }

  render() {
    return (
      <div className="animated fadeIn">
        <Row>
          <Col>
            <Card>
              <CardHeader>
                <i className="fa fa-align-justify"></i> Inventori
              </CardHeader>
              <CardBody>
                <Row>
                    <Col xs="12" md="2"> 
                        <Button color="primary"><i className="icon-plus"></i> Add Barang </Button>
                    </Col>
                </Row>
                <br/>
                <Row>
                    <Col xs="12" md="12">
                        <Table hover bordered striped responsive size="sm">
                          <thead>
                          <tr>
                            <th>Nama</th>
                            <th>Kategori</th>                    
                            <th>Stok</th>
                            <th>Harga</th>
                          </tr>
                          </thead>
                          <tbody>
                            {dataJSON.data.map((value, key) => { 
                                return(
                                    <tr key={key}>
                                        <td>{value.nama}</td>
                                        <td>{value.kategori}</td>
                                        <td>{value.stok}</td>
                                        <td>{value.harga}</td>
                                    </tr>
                                )
                            })}
                          </tbody>
                        </Table>
                    </Col>
                </Row>
                <nav>
                  <Pagination>
                    <PaginationItem><PaginationLink previous tag="button">Prev</PaginationLink></PaginationItem>
                    <PaginationItem active>
                      <PaginationLink tag="button">1</PaginationLink>
                    </PaginationItem>
                    <PaginationItem><PaginationLink tag="button">2</PaginationLink></PaginationItem>
                    <PaginationItem><PaginationLink tag="button">3</PaginationLink></PaginationItem>
                    <PaginationItem><PaginationLink tag="button">4</PaginationLink></PaginationItem>
                    <PaginationItem><PaginationLink next tag="button">Next</PaginationLink></PaginationItem>
                  </Pagination>
                </nav>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>

    );
  }
}

export default Tables;
